.abc {
  text-align: center;

  font-size: 0;
}
.abc > .kigui-o-f {
  width: 140px;
  height: 150px;

  font-size: 1rem;

  margin: 0 5px 10px 5px;
  /*margin-right:0.7em;*/
  /*margin-bottom:0.7em;*/

  display: inline-block;

  overflow: hidden;
}

.abc:not(.open_mode) > .kigui-o-f {
  cursor: pointer;
}

